.slow {
  overflow: hidden;
  -webkit-animation: slow 5s ease forwards;
          animation: slow 5s ease forwards;
}

@-webkit-keyframes slow {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

@keyframes slow {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

.homeButton {
  background-color: #0b0340c4 !important;
  color: #f23869 !important;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 50px !important;
  text-align: center;
  justify-content: center;
  border-radius: 12px;
  margin: 45px;
  padding: 15px;
  width: 250px;
  height: 100px;
}

.flipper {
  -webkit-transition: 0.6s;
  -webkit-transform-style: preserve-3d;
  -moz-transition: 0.6s;
  -moz-transform-style: preserve-3d;
  -ms-transition: 0.6s;
  -ms-transform-style: preserve-3d;
  transition: 0.6s;
  transform-style: preserve-3d;
  position: relative;
}

.flip-container:hover .flipper,
.flip-container.hover .flipper,
#flip-toggle.flip .flipper {
  transform: rotateY(180deg);
  -webkit-filter: FlipH;
          filter: FlipH;
  -ms-filter: "FlipH";
}

