}

.outer-circle span {
  position: absolute;
  height: 200px;
  width: 200px;
  background: linear-gradient(#14ffe9, #ffeb3b, #ff00e0);
  border-radius: 50%;
}

span:nth-child(1) {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

span:nth-child(2) {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

span:nth-child(3) {
  -webkit-filter: blur(25px);
          filter: blur(25px);
}

span:nth-child(4) {
  -webkit-filter: blur(150px);
          filter: blur(150px);
}

.inner-circle {
  height: 180px;
  width: 180px;
  position: absolute;
  background: black;
  top: 10px;
  left: 10px;
  border-radius: 50%;
  z-index: 9;
